<template>
	<view class="content">
		<scroll-view scroll-y="true" class="specification">
			<view v-for="(item, index) in specification" :key="index" class="attr-list">
				<view>
					<text @longpress="setOperation(index,-1)">{{ item.value }}</text>
					<text class="right-delete cuIcon-delete" @tap="deleteSpecification(index)"></text>
				</view>
				<view class="item-list">
					<text
						v-for="(childItem, childIndex) in item.leaf"
						:key="childIndex"
						class="tit clamp"
						:class="{ selected: childItem.selected, disabled: childItem.disabled}"
						@click="selectSpec(index, childIndex)"
						@longpress="setOperation(index,childIndex)"
					>
						{{ childItem.value }}
					</text>
					<button class="cu-btn round bg-orange" @click="addStandardValue(index)">添加规格值</button>
				</view>
			</view>
			<button class="add-btn" @click="addSpecification">添加规格</button>
		</scroll-view>
		<view class="cu-modal" :class="modalName=='RadioModal'?'show':''">
			<view class="cu-dialog">
				<view class="cu-list menu sm-border">
					<view class="cu-item arrow" @tap="modalName = 'DialogModal1'">
						<view>
							<text>编辑</text>
						</view>
					</view>
					<view class="cu-item arrow" @tap="deleteSpecification(-1)">
						<view >
							<text>删除</text>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="cu-modal" :class="modalName=='DialogModal1'?'show':''">
			<view class="cu-dialog">
				<view class="cu-bar bg-white justify-end">
					<view class="action" @tap="hideModal">
						<text class="cuIcon-close text-red"></text>
					</view>
				</view>
				<view class="margin">
					<view class="cu-form-group">
						<view class="title">{{title}}</view>
						<input maxlength="30" class="text-right" :placeholder="title" v-model="name" placeholder-class="placeholder"></input>
					</view>
				</view>
				
				<view class="cu-bar bg-white justify-end">
					<view class="action">
						<button class="cu-btn line-red text-red" @tap="hideModal">取消</button>
						<button class="cu-btn bg-red margin-left" @tap="setSpecification">确定</button>
		
					</view>
				</view>
			</view>
		</view>
		<view v-if="details.ids" class="swiper-bottom">
			<view class="page-body-info" @tap="chooseImage">
			    <view v-if="!details.img" class="image-plus image-plus-nb">
				<view class="cuIcon-add"></view>
			  </view>
			  <view v-else class="image-plus image-plus-nb">
				<image mode="aspectFit" :src="details.img"></image>
			  </view>
			</view>
			
			<view class="cu-form-group">
				<view class="title">成本价</view>
				<input maxlength="60" type="digit" class="text-right" placeholder="成本价" v-model="details.market_price" placeholder-class="placeholder"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">市场价</view>
				<input maxlength="60" type="digit" class="text-right" placeholder="市场价" v-model="details.cost_price" placeholder-class="placeholder"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">销售价</view>
				<input maxlength="60" type="digit" class="text-right" placeholder="销售价" v-model="details.price" placeholder-class="placeholder"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">库存</view>
				<input maxlength="60" type="number" class="text-right" placeholder="库存" v-model="details.inventory" placeholder-class="placeholder"></input>
			</view>
		</view>
		<!-- 底部 -->
		<view class="footer">
			<view class="submit-content go-indent">
				<text class="submit-red" @click="goDetails()">确定</text>
			</view>
		</view>
	</view>
</template>

<script>
	import Good from '../../api/good'
	import { objectURLToBlob, createUniqueString, diffArary } from '../../utils'
	import { param2Data } from '@/components/sku/sku2param'
	import { flatten, creatIds } from '@/components/sku/utils'
	export default {
		data() {
			return {
				multiArray: [
					[],
					[],
					[]
				],
				id: '',
				name: '',
				oid: '',
				details: {
					inventory: 0,
					market_price: 0,
					cost_price: 0,
					price: 0,
					img: '',
					ids: ''
				},
				title: '规格名',
				list: [],
				specification: [],
				specificationId: [],
				multiIndex: [0, 0],
				modalName: '',
				data: [],
				productSkus: []
			}
		},
		onLoad(option){
			if(option.data){
				this.details = JSON.parse(option.data)
				this._setData(this.details)
			}
		},
		computed: {
		    skusList() {
				return flatten(this.specification).map(item => ({
					skus: item.skus,
					ids: creatIds(item.skus)
				}))
		    },
		},
		watch: {
		    skusList: {
		          deep: true,
		          immediate: true,
		          handler(newSkus, oldSkus) {
					  if (!newSkus.length) return (this.data = [])
					  this.details = {
					  	inventory: 0,
					  	market_price: 0,
					  	cost_price: 0,
					  	price: 0,
					  	img: '',
					  	ids: ''
					  }
					  if (!oldSkus || !oldSkus.length) return this.initData(newSkus)
					  if (newSkus.length === oldSkus.length) {
						  this.data = newSkus.map((item, index) => ({
						  ...this.data[index],
						  ...item
						  }))
					    // 当规格名和规格值数量未发生变化，更新 skus 即可
					    return (this.data = newSkus.map((item, index) => ({
					  	...this.data[index],
					  	...item
					    })))
					  }
					  // 当规格名的数量发生了变化
					  if (newSkus[0].skus.length !== oldSkus[0].skus.length) {
					    return this.initData(newSkus)
					  }
					  const diffIds = this.diffIds(newSkus, oldSkus)
					  if (newSkus.length > oldSkus.length) {
					    // 当添加了规格值
					    const data = []
					    newSkus.forEach(item => {
					  	const sku = this.data.find(_item => _item.ids === item.ids)
					  	if (sku) {
					  	  data.push(sku)
					  	} else {
					  	  data.push({
					  		...item,
					  		inventory: 0,
					  		market_price: 0,
					  		cost_price: 0,
					  		price: 0,
					  		img: '',
					  		ids: ''
					  	  })
					  	}
					    })
					    this.data = data
					  } else {
					    // 当删除了规格值
					    this.data = this.data.filter(_item => !diffIds.includes(_item.ids))
					  }
		          }
		        }
		  },
		methods: {
			//如果有数据
			_setData(data) {
			    if (!data || !data.length) return
			    const { productSkus, specification } = param2Data(data)
			    this.specification = specification
				this.productSkus = productSkus
			  },
			//提交
			goDetails(){
				const specification = this.specification
				const data = this.data
				const that = this
				if(specification.length === 0){
					that.$api.msg('请先添加规格')
				}
				// 不允许存在请填写规格名和请填写规格值
				for (const i in specification) {
					if(specification[i].value === '请填写规格名'){
						that.$api.msg('规格名中有未配置的值')
						return false
					}
					if(specification[i].leaf.length > 0){
						for (const j in specification[i].leaf) {
							if(specification[i].leaf[j].value === '请填写规格值'){
								that.$api.msg('规格值中有未配置的值')
								return false
							}
						}
					}else{
						that.$api.msg('请添加规格值')
						return false
					}
				}
				//不允许规格没有配置
				const isNum=/^\d+\.?\d{0,2}$/
				data.map(item =>{
					 const { img, market_price, cost_price, price, inventory, skus } = item
					 const skuText = skus.reduce(
					   (str, prev) => `${str}${prev.k}：${prev.v};`,
					   '',
					 )
					 if(market_price.toString() === ''){
					 	that.$api.msg(skuText + '未输入成本价')
					 	throw new Error('未输入成本价')
					 }
					 if(!isNum.test(market_price)){
						 that.$api.msg(skuText + '成本只能是正整数或小数后保留2位小数')
						 throw new Error('成本只能是正整数或小数后保留')
					 }
					 if(cost_price.toString() === ''){
					 	that.$api.msg(skuText + '未输入市场价')
					 	throw new Error('未输入市场价')
					 }
					 if(!isNum.test(cost_price)){
						 that.$api.msg(skuText + '市场价只能是正整数或小数后保留2位小数')
						 throw new Error('市场价只能是正整数或小数后保留')
					 }
					 if(price.toString() === ''){
					 	that.$api.msg(skuText + '未输入销售价')
					 	throw new Error('未输入销售价')
					 }
					 if(!isNum.test(price)){
						 that.$api.msg(skuText + '销售价只能是正整数或小数后保留2位小数')
						 throw new Error('销售价只能是正整数或小数后保留')
					 }
					 if(inventory.toString() === ''){
					 	that.$api.msg(skuText + '未输入库存')
					 	throw new Error('未输入库存')
					 }
					 if(!/(^[0-9]\d*$)/.test(inventory)){
						 that.$api.msg(skuText + '库存只能是正整数')
						 throw new Error('库存只能是正整数')
					 }
					 item.product_sku = skus.map(sku => ({
						 key: sku.k,
						 value: sku.v
					 }))
				})
				that.$api.prePage().refreshGoodSkuList(data)
				that.$api.msg(`规格配置成功`);
				setTimeout(()=>{
					uni.navigateBack()
				}, 800)
			},
			//获取规格详情
			selectSpec(index,index2){
				let specificationId = this.specificationId
				const that = this
				this.specificationId[index] = this.specification[index].id + '-' + this.specification[index].leaf[index2].id
				if(this.specification[index].value === '请填写规格名'){
					that.$api.msg('规格名中有未配置的值')
					return false
				}
				if(this.specification[index].leaf[index2].value === '请填写规格值'){
					that.$api.msg('规格值中有未配置的值')
					return false
				}
				// 标记当前为选中
				this.specification[index].leaf.forEach((item,index)=>{
					item.selected = false
				})
				this.specification[index].leaf[index2].selected = true
				
				for (const i in this.data) {
					if(this.data[i].ids === this.specificationId.join('_')){
						this.details = this.data[i]
					}
				}
			},
			// 初始化属性
			initData(skusList) {
				if (this.productSkus && this.productSkus.length) {
				  // 初始化数据
				  this.data = this.productSkus
				  return
				}
			    this.data = skusList.map(item => ({
			      ...item,
			      // 初始化属性
			      inventory: 0,
			      market_price: 0,
			      cost_price: 0,
			      price: 0,
			      img: '',
			      ids: ''
			    }))
			},
			diffIds(skusList1, skusList2) {
			    // 两个数据的 ids 进行相差
			    skusList1 = skusList1.map(item => item.ids)
			    skusList2 = skusList2.map(item => item.ids)
			    return diffArary(skusList1, skusList2)
			},
			//删除规格
			deleteSpecification(index){
				const multiIndex = this.multiIndex
				if(index === -1){
					this.specification[multiIndex[0]].leaf.splice(multiIndex[1],1)
					this.hideModal()
				}else{
					this.specification.splice(index,1)
					this.specificationId.splice(index,1)
				}
			},
			//操作
			setOperation(index,index2){
				this.multiIndex = [index,index2]
				if(index2 === -1){
					this.title = '规格名'
				} else {
					this.title = '规格值'
				}
				this.modalName = 'RadioModal'
			},
			hideModal(e) {
				this.modalName = null
				this.name = null
			},
			//添加规格值
			addStandardValue(index){
				this.specification[index].leaf.push({
					id: createUniqueString() + '_id',
					value: '请填写规格值',
					selected: false
				})
				this.title = '规格值'
				this.multiIndex = [index,this.specification[index].leaf.length-1]
				this.modalName = 'DialogModal1'
				
			},
			//添加规格
			addSpecification(){
				this.specification.push({
					value: '请填写规格名',
					id: createUniqueString() + '_id',
					leaf: []
				})
				this.title = '规格名'
				this.multiIndex = [this.specification.length-1, -1]
				this.modalName = 'DialogModal1'
			},
			//修改规格属性
			setSpecification(){
				const multiIndex = this.multiIndex
				this.name = this.name.trim()
				const that = this
				if (!this.name) return
				if(multiIndex[1] === -1){
					const oldArr = this.specification
					if(oldArr.some(option => option.value === this.name)){
						that.$api.msg('规格名已存在');
						return false
					}
					this.specification[multiIndex[0]].value = this.name
				}else{
					const oldArr = this.specification[multiIndex[0]].leaf
					if(oldArr.some(option => option.value === this.name)){
						that.$api.msg('规格值已存在');
						return false
					}
					this.specification[multiIndex[0]]['leaf'][multiIndex[1]].value = this.name
				}
				this.name = null
				this.hideModal()
			},
			async getList(){
				
			},
			//主图
			chooseImage() {
				let that = this
				let uploadFile = ''
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					success: (res) => {
						uni.uploadFile({
						  url: uni.getStorageSync('applyGoodSecret').host + 'uploadPictures',
						  filePath: res.tempFilePaths[0],
						  name: 'file',
						  header: {
							'apply-secret': uni.getStorageSync('applyGoodSecret').secret,
							'Authorization': 'Bearer ' + uni.getStorageSync('applytoken')
						  },
						  formData: {
							type: 1,
							size: 1024 * 500
						  },
						  success(res) {
							that.details.img =res.data
						  },
						  fail(res) {
							that.$api.msg(res.message);
							return false
						  }
						})
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: $page-color-base;
		padding-top: 16upx;
	}
	.swiper-bottom{
		padding-bottom: 80upx;
	}
	.row{
		display: flex;
		align-items: center;
		position: relative;
		padding:0 30upx;
		height: 110upx;
		background: #fff;
		
		.tit{
			flex-shrink: 0;
			width: 120upx;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.input{
			flex: 1;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.icon-shouhuodizhi{
			font-size: 36upx;
			color: $font-color-light;
		}
	}
	.default-row{
		margin-top: 16upx;
		.tit{
			flex: 1;
		}
		switch{
			transform: translateX(16upx) scale(.9);
		}
	}
	.add-btn{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 690upx;
		height: 80upx;
		margin: 60upx auto;
		font-size: $font-lg;
		color: #fff;
		background-color: $base-color;
		border-radius: 10upx;
		box-shadow: 1px 2px 5px rgba(219, 63, 96, 0.4);
	}
	.goods-section {
		background: #fff;
		padding-bottom: 1px;
	
		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}
	
		.logo {
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}
	
		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}
	
		.g-item {
			display: flex;
			margin: 20upx 30upx;
	
			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}
	
			.right {
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}
	
			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}
	
			.spec {
				font-size: 26upx;
				color: $font-color-light;
			}
	
			.price-box {
				display: flex;
				align-items: center;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;
	
				.price {
					margin-bottom: 4upx;
				}
				.number{
					font-size: 26upx;
					color: $font-color-base;
					margin-left: 20upx;
				}
				.delete-button{
					position:absolute;
					right:20upx;
				}
			}
			.text-icon{
				font-size: 40upx;
			}
			.step-box {
				position: relative;
			}
		}
	}
	.page-body-info {
	  display: flex;
	  box-sizing: border-box;
	  padding: 30rpx;
	  padding-top:60rpx;
	  height: 800rpx;
	  background-color: #ffffff;
	  border-bottom: 1rpx solid #eee;
	  align-items: center;
	  justify-content: center;
	}
	.page-body-info image{
	  height: 800rpx;
	}
	.image-plus-nb .cuIcon-add{
	  font-size: 120rpx;
	  color: #eee;
	}
	.specification{
		background-color: #FFFFFF;
		margin-bottom: 20upx;
		padding: 30upx;
		padding-top: 0;
		padding-bottom: 0;
		position: sticky;
		top: var(--window-top);
		z-index: 1;
		flex-direction: row;
		max-height: 250px;
	}
	.attr-list {
		width: 100%;
		position: relative;
		display: flex;
		flex-direction: column;
		font-size: $font-base + 2upx;
		color: $font-color-base;
		padding-top: 30upx;
		padding-left: 10upx;
	}
	.item-left{
		display: flex;
		width:120upx;
		margin-bottom: 60upx;
	}
	.item-list {
		padding: 20upx 0 0;
		display: flex;
		flex-wrap: wrap;
		text {
			display: flex;
			align-items: center;
			justify-content: center;
			background: #eee;
			margin-right: 20upx;
			margin-bottom: 20upx;
			border-radius: 100upx;
			min-width: 60upx;
			height: 60upx;
			padding: 0 20upx;
			font-size: $font-base;
			color: $font-color-dark;
		}
		.selected {
			background: #fbebee;
			color: $uni-color-primary;
		}
		.disabled {
			color: $font-color-disabled;
		}
	}
	.right-delete{
		float: right;
	}
	.cu-modal{
		z-index: 5;
	}
	.footer{
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 995;
		display: flex;
		align-items: center;
		width: 100%;
		height: 90upx;
		justify-content: space-between;
		font-size: 30upx;
		background-color: #fff;
		z-index: 998;
		color: $font-color-base;
		box-shadow: 0 -1px 5px rgba(0,0,0,.1);
		.price-content{
			padding-left: 30upx;
		}
		.price-tip{
			color: $base-color;
			margin-left: 8upx;
		}
		.price{
			font-size: 36upx;
			color: $base-color;
		}
		.submit{
			display:flex;
			align-items:center;
			justify-content: center;
			width: 280upx;
			height: 100%;
			color: #fff;
			font-size: 32upx;
			background-color: $base-color;
		}
		.submit-content{
			display:flex;
			flex-direction:row-reverse;
			align-items:center;
			justify-content: center;
			height: 100%;
			.submit-red{
				display:flex;
				flex-direction:row-reverse;
				align-items:center;
				justify-content: center;
				width: 100%;
				height: 100%;
				color: #fff;
				font-size: 32upx;
				background-color: $base-color;
			}
			.submit-default{
				display:flex;
				align-items:center;
				justify-content: center;
				width: 200upx;
				height: 100%;
				color: #fff;
				font-size: 32upx;
				background-color: #909399;
			}
		}
		.go-indent{
			width: 100%;
		}
	}
</style>
